import React, { useState, useEffect } from 'react';
import { useSwipeable } from 'react-swipeable';
import LoadingScreen from './components/LoadingScreen';
import './App.css';
import movie01 from "/Users/visiblyankerd/Desktop/GITHUB/swipemenu/src/assets/carnitaMainMenuSaleMP.mp4"
import img2 from "/Users/visiblyankerd/Desktop/GITHUB/swipemenu/src/assets/specialMenuBackground.png"
import img3 from "/Users/visiblyankerd/Desktop/GITHUB/swipemenu/src/assets/tacossuaperro.png"
import Movie02 from "/Users/visiblyankerd/Desktop/GITHUB/swipemenu/src/assets/specialOrderCuchu.mp4"
import img5 from "/Users/visiblyankerd/Desktop/GITHUB/swipemenu/src/assets/tacossuaperro.png"

const App = () => {
  const [currentTab, setCurrentTab] = useState(0);
  const [loading, setLoading] = useState(true);
  const [currentContentIndex, setCurrentContentIndex] = useState(0);
  const contentToDisplay = [
    [img2,img3],
    [img5,movie01],
    [Movie02,img3],
  ];

  useEffect(() => {
    const timer = setTimeout(() => setLoading(false), 2000); // Simulate loading
    return () => clearTimeout(timer);
  }, []);

  const handleTabSwitch = (newTabIndex) => {
    if (newTabIndex >= 0 && newTabIndex < contentToDisplay.length) {
      setCurrentTab(newTabIndex);
    }
  };

  // Function to switch content within the tab
  const handleContentSwitch = (newContentIndex) => {
    const currentTabContent = contentToDisplay[currentTab];
    if (newContentIndex >= 0 && newContentIndex < currentTabContent.length) {
      setCurrentContentIndex(newContentIndex);
    }
  };

  // Swipe handlers
  const handlers = useSwipeable({
    onSwipedLeft: () => handleTabSwitch(currentTab + 1),
    onSwipedRight: () => handleTabSwitch(currentTab - 1),
    onSwipedUp: () => handleContentSwitch(currentContentIndex + 1),
    onSwipedDown: () => handleContentSwitch(currentContentIndex - 1),
  });

  const currentContent = contentToDisplay[currentTab][currentContentIndex];
  const isVideo = currentContent.endsWith('.mp4');
  const contentClassName = `video-container tab-${currentTab}`;
return (
  <div {...handlers} className="app">
    {loading ? (
      <LoadingScreen />
    ) : (
      <>
        <div className={contentClassName}>
          {isVideo ? (
            <video src={ isVideo }  loop autoPlay />
          ) : (
            <img src={currentContent}  alt="img" />
          )}
        </div>
        <div className="tab-menu">
          {['Specials', 'Lunch', 'Dinner'].map((tabName, index) => (
            <div
              key={index}
              className={`tab ${currentTab === index ? 'active' : ''}`}
              onClick={() => handleTabSwitch(index)}
            >
              {tabName}
            </div>
          ))}
        </div>
      </>
    )}
  </div>
);
};

export default App;


